import React from 'react';
import { View, Text, TouchableOpacity, Image, StyleSheet, Clipboard } from 'react-native';

const JobDetailsScreen = ({ route }) => {
  const { image } = route.params;

  const copyImageURIToClipboard = () => {
    Clipboard.setString(image);
    alert('Ссылка на изображение скопирована в буфер обмена');
  };

  return (
    <View style={styles.container}>
      <Image source={{ uri: image }} style={styles.image} />

      <TouchableOpacity style={styles.button} onPress={copyImageURIToClipboard}>
        <Text style={styles.buttonText}>Поделится</Text>
      </TouchableOpacity>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    justifyContent: 'center',
    alignItems: 'center',
  },
  image: {
    width: 300,
    height: 300,
    marginBottom: 20,
    borderRadius: 10,
  },
  button: {
    backgroundColor: 'black', 
    padding: 10,
    borderRadius: 5,
  },
  buttonText: {
    color: 'white',
    fontWeight: 'bold',
  },
});

export default JobDetailsScreen;
