import React, { useState, useEffect } from 'react';
import { StyleSheet, Text, Image, TouchableOpacity, ScrollView, View, FlatList } from 'react-native';
import * as ImagePicker from 'expo-image-picker';
import { firebase } from '../../config';
import { useNavigation } from '@react-navigation/native';

export default function App() {
  const [images, setImages] = useState([]);
  const [uploading, setUploading] = useState(false);
  const [photoURLs, setPhotoURLs] = useState([]);
  const [selectedImage, setSelectedImage] = useState(null);

  const navigation = useNavigation();

  useEffect(() => {
    const fetchImages = async () => {
      const db = firebase.firestore();
      const imagesRef = db.collection('images');

      // Получить все изображения из Firestore
      imagesRef
        .get()
        .then(querySnapshot => {
          const imageUrls = [];
          querySnapshot.forEach(doc => {
            const data = doc.data();
            // Проверка, существует ли поле photoURL в документе
            if (data.photoURL) {
              imageUrls.push(data.photoURL);
            }
          });
          setImages(imageUrls);
        })
        .catch(error => {
          console.error('Ошибка при получении изображений:', error);
        });
    };

    fetchImages();
  }, []);

  const handleImageUpload = async () => {
    let result = await ImagePicker.launchImageLibraryAsync({
      mediaTypes: ImagePicker.MediaTypeOptions.Images,
      aspect: [4, 3],
      quality: 1,
    });

    if (!result.cancelled) {
      const blob = await new Promise((resolve, reject) => {
        const xhr = new XMLHttpRequest();
        xhr.onload = function () {
          resolve(xhr.response);
        };
        xhr.onerror = function () {
          reject(new TypeError('Network request failed'));
        };
        xhr.responseType = 'blob';
        xhr.open('GET', result.uri, true);
        xhr.send(null);
      });

      const uid = firebase.auth().currentUser.uid;
      const imageName = `profile_${uid}_${Date.now()}.jpg`;
      const ref = firebase.storage().ref().child(`Hakaka/${imageName}`);
      const snapshot = ref.put(blob);

      snapshot.on(
        firebase.storage.TaskEvent.STATE_CHANGED,
        () => {
          setUploading(true);
        },
        (error) => {
          setUploading(false);
          console.log(error);
          blob.close();
          return;
        },
        () => {
          snapshot.snapshot.ref.getDownloadURL().then(async (downloadURL) => {
            setUploading(false);
            console.log('Download URL: ', downloadURL);

            // Сохраните ссылку в Firestore в коллекции "images"
            const imagesRef = firebase.firestore().collection('images');
            await imagesRef.add({ photoURL: downloadURL });

            // Обновить список изображений
            setPhotoURLs([...photoURLs, downloadURL]);
          });
        }
      );
    }
  }

  const handleImageSelect = (url) => {
    setSelectedImage(url);
  }

  return (
    <ScrollView contentContainerStyle={styles.scrollViewContainer}>
      <TouchableOpacity onPress={handleImageUpload}>
        <Text style={styles.uploadText}>Загрузить фотографию</Text>
      </TouchableOpacity>

      <View style={styles.imageListContainer}>
        <FlatList
          data={photoURLs}
          renderItem={({ item }) => (
            <TouchableOpacity
              onPress={() => handleImageSelect(item)}
            >
              <Image source={{ uri: item }} style={styles.image} />
            </TouchableOpacity>
          )}
          keyExtractor={(item, index) => index.toString()}
          style={styles.imageList}
        />
      </View>

      {/* {selectedImage && (
        <View style={styles.selectedImageContainer}>
          <Image source={{ uri: selectedImage }} style={styles.selectedImage} />
        </View>
      )} */}
    </ScrollView>
  );
}

const styles = StyleSheet.create({
  scrollViewContainer: {
    flexGrow: 1,
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: 'white',
  },
  uploadText: {
    fontSize: 18,
    fontWeight: 'bold',
    color: 'blue',
    marginBottom: 20,
  },
  imageListContainer: {
    width: '100%',
    justifyContent: 'center',
    alignItems: 'center',
  },
  imageList: {
    width: '100%',
    marginBottom: 20,
  },
  image: {
    width: 150,
    height: 150,
    marginBottom: 10,
  },
  selectedImageContainer: {
    width: '100%',
    alignItems: 'center',
  },
  selectedImage: {
    width: 300,
    height: 300,
  },
});
